html {
  min-height:100%;
  position: relative;
}
body {
  font-family: 'Open Sans', sans-serif;
  background-color: rgb(191, 210, 250 );
  /* height: 100%; */
}

header {
  color: #fff;
  background-color: #103D5D;
  padding: 10px;
}

header h1 {
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
}

header .wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.logInInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.rightAlign {
  margin-left: 0 auto;
  font-size: 14px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-right: 30px;
}
header .wrapper .buttonHeader {
  border-radius: 5px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  color: #fff;
  font-size: 14px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 5px 15px;
}

.container {
  display: -ms-flexbox;
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 10px;
}
.flex {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  line-height: 55px;
  -ms-flex-align: center;
      align-items: center;  
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.add-car,
.modify-car {
  border-radius: 3px;
  border-top: 3px solid #103D5D;
  background-color: rgb(171, 192, 240);
  padding: 5px;
  -webkit-box-shadow: 
    0 3px 4px 0 rgba(0,0,0,.14), 
    0 3px 3px -2px rgba(0,0,0,.12), 
    0 1px 8px 0 rgba(0,0,0,.2);
          box-shadow: 
    0 3px 4px 0 rgba(0,0,0,.14), 
    0 3px 3px -2px rgba(0,0,0,.12), 
    0 1px 8px 0 rgba(0,0,0,.2);
  margin-bottom: 10px;  
  width: 100%;
}
.modify-car {
  position: absolute;
  top: 200px;
  max-width: 1100px;
  min-height: 40px;
  max-height: 200px;
  -webkit-box-shadow: 4px 4px 12px #999;
          box-shadow: 4px 4px 12px #999;
  background-color: rgb(128, 158, 228);
}
.add-car form,
.modify-car form {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  font-size: 14px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
form > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
form input, form .is-active, form button {
  background-color: rgb(224, 231, 247);
  font-size: 14px;
  color: #000;
  padding: 4px 5px;
  margin: 4px 0px;
  border: 0;
  width: 80%;
}

form button {
  border-radius: 5px;
  background-color: #FA6900;
  cursor: pointer;
  color: #fff;
  padding: 5px 0px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
}

.invalid-new-car-data-alert {
  display: block;
  font-size: 12px;
  color: rgb(190, 11, 11);
  margin-left: 20px;
}
/* .invalid-new-car-data-alert > div {
  display: block;
} */

.white-bg {
  position: absolute;
  top: 0px;
  /* left: 0px; */
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-transition: background-color 900ms ease;
  -o-transition: background-color 900ms ease;
  transition: background-color 900ms ease;
}
.white-bg:hover {
  -webkit-transition: background-color 900ms ease;
  -o-transition: background-color 900ms ease;
  transition: background-color 900ms ease;
  background-color: rgba(255, 255, 255, 0.7);
  
}
.pop-up {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 260px;
  height: 120px;
  background-color: #f1f1f1;
  border: 1px solid #8a8989;
  border-top: 8px solid rgb(219, 17, 17);
  margin-right: -130px;
  padding: 10px;
  font-weight: 700;
  border-radius: 8px 0 8px 8px;
  -webkit-box-shadow: 4px 4px 12px #999;
          box-shadow: 4px 4px 12px #999;
}

.pop-up::after {
  content: '';
  position: absolute;
  top: -8px;
  right: -20px;
  width: 0;
  height: 0;
  border-left: 20px solid rgb(219, 17, 17);
  border-top: 0px solid transparent;
  border-bottom: 8px solid transparent;
}
.pop-up p {
  text-align: center;
  margin: 0;
}
.pop-up .buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.pop-up .buttons .button {
  color: #fff;
  padding: 5px 20px;
  border-radius: 4px;
  background-color: rgb(110, 170, 225);
  cursor: pointer;
  -webkit-box-shadow: 1px 2px 3px 0px rgb(50, 64, 73);
          box-shadow: 1px 2px 3px 0px rgb(50, 64, 73);
}
.cancel-modify-car {
  background-color: rgb(110, 170, 225);
}
.pop-up .buttons .delete {
  background-color: rgb(245, 48, 22);
}

.invisible {
  display: none;
}

h2 h3 {
  font-size: 26px;
  font-weight: 300;
  color: #103D5D;
}

.display-cars {
  width: 100%;
}

.user-profile {
  position: fixed;
  bottom: 20px;
  right: 40px;
  width: 100px;
  height: 100px;
}

.user-profile img {
  border-radius: 50%;
  max-width: 100%;
}

footer {
  margin-top: 20px;
  color: #fff;
  background-color: #103D5D;
  padding: 5px 0 60px 0;
  font-size: 16px;
  text-align: right;
  height: 50px;
}
footer p {
  margin-bottom: 20px;
}


